.explore-container{
  padding-top: 75px;
  padding-left: 45px;
  .topContainer{
    padding: 17px;
    .form{
      padding-top: 25px;
    }
  }
  .profile__information{
    padding: 30px 67px;
  }
  .information-container{
    padding: 12px 12px;
  }
  .information-title {
    font-size: 14px;
    font-weight: bold;
  }
  .information-label{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

}
.entity-top-container{
  text-align: center;
  padding-top: 15px;
  cursor: pointer;
}
.bg-success{
  background-color: #19d895 !important;
}
.aligner-wrapper .absolute.bottom{
  bottom: 0;
}
.absolute{

  position: absolute;
}
.v-strock-2{
  width: 3px;
}
.h-100 {
  height: 100% !important;
}
.absolute.top{
  top: 0;
}
.mb-2{
  margin-bottom: 0.5rem !important;
}

.explore-entity-view{
  height: calc(100% - 1em) !important;

  @include themify($themes) {
    background: themed('colorBackgroundBody');
    box-shadow: 0 0 0 0 #fff, inset 0 0 0 rgba(0,0,0,.08), 0 0 0 0 rgba(0,0,0,.05);
  }

  .profile__information{
    @include themify($themes) {
      border-bottom: 1px solid themed('colorBorder');
    }
    .profile__avatar{
      height: 140px;
      width: 140px;
      overflow: hidden;
      border-radius: 50%;
    }
  }

}