.datePickerContainer{
    background-color: #fbfcfd;
    background-repeat: no-repeat;
    background-size: 0% 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: stretch;
    align-items: stretch;
    padding: 1px;
    height: 34px;
   
}
.dateRangeLable {
    margin-top: 8px;
    font-size: 0.75rem;
    line-height: 1.5;
    color: #1a1c21;
    /* font-weight: 600; */
    padding-left: 15px;
    display: inline-block;
    transition: all 150ms cubic-bezier(0.694, 0.0482, 0.335, 1);
}


.quickSelectContainer{
    padding-top: 6px;
    border-bottom: 2px solid whitesmoke;
}
.commonlyUsedGrid{
    list-style: none;
    display: block;
    column-gap: 0;
    align-items: stretch;
    margin-bottom: 5px;
    & li{
        font-size: 14px;
        margin-top:0px;

       
        color: #2465cc;
        cursor: pointer;
    }
    
}
.react-datepicker__input-container {

    & input{
        margin-top:4px;
        width:140px
    } 
    
}
li.react-datepicker__time-list-item--selected{
    @include themify($themes) {
        background:  #0066CC !important;
    }
}
.react-datepicker__header{
    @include themify($themes) {
        background:  themed('colorBackground') !important;
    }
}
.react-datepicker__day--in-range{
    background:  #0066CC !important;
}
.popover{
    max-width: 400px  !important;
}