.header-container{
  height: 45px;
}

.sideBar{
  @include themify($themes) {
    background:themed('colorBackground') !important;
  }
  .dscFieldChooser{
    padding-left: 0px;
  }
  .dropDownButtonWidth{
    height: 40px;
  }
  .dropDownButtonWidth button{
    width: 100%;
    color: #1a1c21;
    font-size: 16px;
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 600;
    letter-spacing: -0.02em;
    background: transparent !important;
    text-align: left;

  }
  .availableTitleHeader{
    padding-left: 7px;
    height: 24px;

    padding-top: 8px;
    padding-bottom: 23px;

  }
  .dscFieldList{
    list-style: none;
    margin-bottom: 0;
    padding-left: 20px;
  }
  .fieldName{

    @include themify($themes) {
      color:themed('colorText') !important;
    }
    flex-grow: 1;
    word-break: break-word;
    font-weight: 700;
  }
  .dscFieldList li{
    margin-bottom: 12px ;
    cursor: pointer;
  }
  li div.actionContainer {
    opacity: 0;
  }
  li:hover div.actionContainer {
    opacity: 1;
  }




  .availableTitleHeader h4{
    font-size: 0.75rem;
    line-height: 1.5rem;
    font-weight: 700;
  }

  .searchContainer{
    padding-left: 7px;
  }
  .dscCollapsibleSidebar__collapseButton{
    position: absolute;
    top: 0;
    right: 0px;
    cursor: pointer;
    min-height: 12px;
    min-width: 12px;
    padding: 2px;
  }
}
th span.colDelete{
  opacity: 0;
  font-size: 12px;
}

th:hover span.colDelete {
  opacity: 1;
  cursor: pointer;
}


.euiButtonEmpty .euiButtonEmpty__text{
  @include themify($themes) {
    color:themed('linkColor') !important;
  }
  cursor: pointer;
}

td.filterRow{
  white-space: pre-wrap;
  overflow-wrap: break-word !important;
  word-break: break-all !important;



}

td.filterRow  span{
  opacity: 0;
  cursor: pointer;

}

td.filterRow:hover  span{
  opacity: 1;
  cursor: pointer;

}
tr.documentTable  span{
  opacity: 0;
  cursor: pointer;
}
tr.documentTable:hover  span{
  opacity: 1;
  cursor: pointer;

}


.dscWrapper__content{
  padding-top: 4px;
  background-color: #FFF;
  ox-shadow: 0 2px 2px -1px rgba(152, 162, 179, 0.3), 0 1px 5px -2px rgba(152, 162, 179, 0.3);
  background-color: #FFF;
  border: 1px solid #D3DAE6;
  border-radius: 4px;
  flex-grow: 1;
}
.source{
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: top;

}
.truncate-by-height{
  max-height: 115px !important;
  display: inline-block;
  overflow: hidden;
}
dl.source{
  margin-bottom: 0;
  line-height: 2em;
  word-break: break-word;
}
dt {
  background-color: rgba(0, 86, 144, 0.1);
  @include themify($themes) {
    color:themed('colorText') !important;
  }

  padding: 2px 4px;
  margin-right: 4px;
  word-break: normal;
  border-radius: 4px;
  display: inline;
  margin-left: 2px;
}
.MuiSvgIcon-colorPrimary{
  color: #1BA9F5;
}
dd{
  display: inline;
}
.expandedDocumentTitle{
  font-size: 0.875rem;
  line-height: 1.3125rem;
  color: #1a1c21;

  line-height: 1.5rem;
  font-weight: 600;
  letter-spacing: -0.02em;
}
.documentContainer{
  margin-top:15px;
}
.documentTableKey{
  width: 10%;
  font-weight: bold !important;
  color: black !important;;
}
.historicalContainer{
  height: 100vh;
  overflow-y:auto ;
}
.documentTableButtons{
  width: 10%;
}
.documentTable{
  border-bottom: 1px solid transparent !important
}
.btn.icon{
  padding:5px 9px !important
}
