.list-group-item{
    position: relative;
    display: block;
    padding: 0.75rem 1.25rem;
    background-color: transparent;
    border: 0px solid rgba(0, 0, 0, 0.125); 
    text-align: left;
    cursor: pointer;
}
.widgetTitle{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
}

.list-group-item:hover{
   background: rgb(231 226 226);
   width: 100%;
}

.widgetIcon{
    height: 25px;
    width: 25px;
    margin-top: -11px;
}

.widgetIconTitle{
    font-size: 14px;
    padding: 16px 16px;
    margin-bottom: -4px;
    cursor: pointer;
    width: 80%;
   
}
.widgetDelete{
    
    float: right;
   
   
}