.splitter-layout{

    .card{
        @include themify($themes) {
              background-color: themed('colorBackground') !important;
           }

    }
    height: calc(100% - 1em) !important;
    @include themify($themes) {
        background: themed('colorBackground') !important;
     }
    .nav-tabs{
         @include themify($themes) {
           border-bottom: 1px solid  themed('colorBorder') !important;

         }
    }

    .evidenBorder{
         @include themify($themes) {
           border-bottom: 1px solid  themed('colorBorder') !important;
         }
    }
    .case__status{
        cursor: pointer;
        height: 72px;
        width: 12px;

    }
    .layout-splitter{
        @include themify($themes) {
            background-color: themed('colorBorder') !important;

        }
    }
    .form-control{
        background: transparent;
        @include themify($themes) {
          color: themed('colorText')

        }
    }
    .case-card__status{
        display: grid;
         grid-gap: 16px;
         padding: 2px 2px 10px 21px;
    }
    .case__status--high {
        border-left: 10px solid #faa329;
    }
    .case__status--critical {
        border-left: 10px solid red;
    }
    .case__status--medium {
        border-left: 10px solid  #FFF5C3;
    }
    .case__status--low {
        border-left: 10px solid  #04C0F7;
    }

    
    .alert-card-container{
        @include themify($themes) {
            background-color: themed('colorBackgroundBody') !important;
            box-shadow :2px 2px themed('colorScrollbar') !important;
            height: 100px;
       }
       
    }
    .alert-card-body-container{
        @include themify($themes) {
            background-color: themed('colorBackgroundBody') !important;
            
       }
       
    }
    .alertContainer{
        padding-left: 12px;
        max-height: 500px;
        overflow-y: scroll;
        padding-right: 12px;
    }
    .alertEvidenceCard{
        padding-bottom: 12px;
        cursor: pointer;
        @include themify($themes) {
            color: themed('colorText') !important;

        }
    }
    .case-wall-list--item .item-comment-container{
        flex: 1;
    }
    .user-timestamp-container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 345px;
        padding-right: 12px;       
    }
    .activity-user-box{
        display: flex;
        align-items: center;
    }
    .activity-user-box .user{
        display: flex;
        align-items: center;
        margin: 0 10px;
    }
    .activity-user-box .time{
        font-size: 14px;
white-space: nowrap;
text-overflow: ellipsis;
overflow: hidden;
    }
    .action-comment-container{
        display: block;
        padding: var(--top-bottom-padding) 0;
    }
    .action-comment-container .item-comment{
        position: relative;
    }
    .activity-icon-container {
        display: flex;
        width: 60px;
        height: 46px;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    .smp-icon-size--small{
        width: 24px;
        min-width: 24px;
        height: 24px;
    }
    .alert-title{
        font-size: 16px;
        font-weight: bold;
    }
    .alert-title span {
        font-weight: bold;
    }
    .context-details-container__title .icon{
        font-size: 32px;
        position: relative;
        grid-column: 1/2;
        grid-row: 1/3;
        margin-top: 8px;
        @include themify($themes) {
            color: themed('colorText') !important;

        }
        
    }
    .context-details-container__search{
        padding: 0 3px;
        border-top: 1px solid #dee2e6;
        border-bottom: 1px solid #dee2e6;
    }
    .imgContainer{
        height: 32px;
        width: 32px;
    }
    .entityUser{
        padding-top: 8px;
        padding-left: 6px;
    }
    .tn-search--naked-search-input{
        border: 1px solid transparent;
        transition: none;
        width: 100%;
        display: flex;
        align-items: center;
       
        padding-left: 12px;
        height: 33px;   
    }
    .search-icon--size--small{
        width: 24px;
        min-width: 24px;
        height: 24px;
        color: var(--text-secondary-color);
        font-size: 20px;
        opacity: .5;
    }
    .tn-search__input-simp-input{
        background-color: transparent;
        width: 100%;
    }
    .context-details-container__title .description{
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;

        @include themify($themes) {
           color: themed('colorText') !important;

        }
    }
    .collapse__wrapper.with-shadow{
        @include themify($themes) {
            background: themed('borderButton') !important;

        }
    }
    .context-details-container__title{
        display: grid;
        grid-template-columns: auto 1fr;
        grid-gap: 0 12px;
        padding: 0 9px;
        margin: 14px 0;
    }
    .context-details-container{
        display: flex;
        flex-direction: column;
        height: 100%;
        @include themify($themes) {
            background-color: themed('casesBackground') !important;
            
       }
    
    }
    .buttonContainer{
        padding-right: 10px;
        padding-top: 10px;
    }
    .page-content__header--right{
        
        padding-right: 12px;
        padding-top: 17px;
        font-size: 16px;
    
    }
    .ui-table table{
        border-collapse: collapse;
        width: 100%;
        table-layout: fixed;
    }
    .ui-table-scrollable-wrapper{
        position: relative;
    }
    .case-wall-list--item{
        position: relative;
        display: grid;
        grid-template-columns: auto 1fr auto;
        align-items: center;
        justify-content: space-between;
        min-height: 46px;
        border-bottom: 1px solid #cccccc;
       
    }
    .alert-card__content_sub{
        margin-bottom: 0.45rem;
    }
    .alert-card-container-critical{
        border-left : 3px solid red;
    }
    .alert-card-container-high{
        border-left : 3px solid #ffa500;
    }
    .alert-card-container-medium{
        border-left : 3px solid #FFF5C3;
    }
    .alert-card-container-low{
        border-left : 3px solid #04C0F7;
    }
    .wall-case-container__case-comments{
        overflow: auto;
        height: calc(100vh - 277px);
        margin-top: 12px;
        padding: 18px;
    }
    .alert-card__content__name{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: bold;
    }
    .u-ellipsis{
        white-space: nowrap;
        overflow: hidden;
      text-overflow: ellipsis;
    }
    .case-header-assignee{
        display: grid;
         grid-template-columns: auto 1fr;
        grid-gap: var(--grid-small-gap);
    }
    .middle.two-columns{
        grid-template-columns: minmax(100px,2fr) 1fr;
        display: grid;
        grid-gap: 10px;
    }
    .description-case-automation{
        padding: 20px;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        
        margin: 0 12px;
        @include themify($themes) {
            color: themed('colorText') !important;

        }
    }

    .ob-avatar__container{
        position: relative;
        display: flex;
        align-items: center;
        margin-left:11px;
        border-radius: 50%;
        background-color: #faa329;
    }
    .description-case {
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        width: 500px;
        margin: 0 var(--element-margin-medium);
    }
    
    .user-image {
        width: 30px;
        height: 30px;
        @include themify($themes) {
             color: themed('colorBackground');
        }
        font-weight: 600;
        border: 0 solid transparent;
        
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .cases-content__start {
      height: 100%;

      transition: width 0.3s ease-in-out;
      position: relative;

      @include themify($themes) {
        background-color: themed('casesBackground');
      }

      .left-sidebar__header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        .head-text{
            font-size: 16px;
            font-weight: bold;
             @include themify($themes) {
             color: themed('colorText');
            }
        }
        .action-group{
            display: flex;
            align-items: center;
           .ob-action {
               background: 0 0;
                   display: inline-flex;
                   align-items: center;
                   position: relative;
                   margin-left: 7px;
                   cursor: pointer;
                   font-size: 14px;
               @include themify($themes) {
                   color: themed('colorText');
               }
           }

        }



      }

        .case-card__right.active{
            border-bottom: 3px solid #2466cc;
        }

       .left-sidebar__field{
                  width: 100%;
                  display: block;
                  .case-queue-list{
                      height: calc(100vh - 141px);

                      .case-card{
                          width: 100%;
                          display: block;
                          position: relative;
                          align-items: center;
                          margin-bottom: 10px;
                          cursor: pointer;
                          .status--high {
                              box-shadow: inset 15px 0 0 -6px #ffa500;
                           }
                           .status--low {
                              box-shadow: inset 15px 0 0 -6px #04C0F7;
                           }
                           .status--medium {
                              box-shadow: inset 15px 0 0 -6px #FFF5C3;
                            }
                            .status--critical {
                                box-shadow: inset 15px 0 0 -6px red;
                            }
                            .case-card__left active{
                                background: red;
                            }
                           .case-card__left--top{
                            display: grid;
                            align-items: center;
                            grid-template-columns: auto 1fr;
                            margin-left:10px;
                            padding:9px;
                            @include themify($themes) {
                                background: themed('colorBackgroundBody');
                             }
                           }
                           .case-card__name{
                               @include themify($themes) {
                                   color: themed('colorText');
                               }
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            grid-column: 2/3;
                            grid-row: 1/2;

                           }
                           .case-card__time-id{
                               @include themify($themes) {
                                   color: themed('colorText');
                               }
                            grid-column: 1/3;
                            grid-row: 2/3;
                            margin-top: 2px;
                                span {
                                    margin : 0 2px
                                }
                           }
                           .queue-case-info-icons{
                            display: flex;
                            justify-content: space-between;
                            flex-direction: row-reverse;
                               @include themify($themes) {
                                   background: themed('colorBackgroundBody');
                               }


                            margin-left: 10px;

                           }
                           .case-card__round-actions{
                            display: flex;
                            list-style: none;
                            li  {
                                margin-right:10px
                            }
                           }
                      }


                  }

              }

    }
    
    .main-content__left{
        height: 100%;
        position: relative;
        @include themify($themes) {
            background: themed('colorBackground');
         }
    }
    .DraftEditor-editorContainer{
        @include themify($themes) {
            background: themed('casesBackground');
        }
    }
    
    .page-content__center--content{
        display: grid;
        grid-template-rows: auto 1fr;
        grid-template-columns: auto minmax(auto, 3fr) auto;
        width: 100%;
        
        position: relative;
        @include themify($themes) {
            background: themed('casesBackground');
            color:themed('colorText');
         }

      

        .page-content__header--left{
            height: 100%;
            display: grid;
            align-items: center;
            grid-template-columns: auto 1fr;
            grid-gap: 12px;
            border-right: 1px solid #cccccc;
            padding-right: 12px;
        }
        .page-content__header{
            width: 100%;
            height: 100%;
            @include themify($themes) {
                background: themed('casesBackground');
            }
            padding: var(--sides-padding) var(--element-padding-small) var(--sides-padding) var(--element-padding-super-big);
            display: grid;
            grid-gap: var(--grid-main-gap);
            grid-template-columns: auto minmax(auto, 3fr) auto;
            align-items: center;
            .information-container{
                display: grid;
                grid-template-rows: auto 1fr;
                 margin-left: 12px;
                 margin-right: 12px;
            }
        }
        
    }
}

