.splitter-layout{

    .vertical-timeline-element-icon{
          width: 32px;
          height: 32px;
          font-size: 17px;
          padding-top: 4px;
          padding-left: 10px;
         @include themify($themes) {
             background: themed('colorBackgroundBody');
             box-shadow: 0 0 0 0 #fff, inset 0 0 0 rgba(0,0,0,.08), 0 0 0 0 rgba(0,0,0,.05);
        }
    }

    .vertical-timeline::before{
            content: '';
            position: absolute;
            top: 38px;
            left: 14px;
            height: 100%;
            width: 4px;
            background: #cccccc;
    }
    .vertical-timeline-element-content{
        position: relative;
            margin-left: 36px;
             background: transparent;
            border-radius: .25em;
            padding: 1em;

             box-shadow: 0 0px 0 #ddd;
    }
    .case-investigation-header{
        @include themify($themes) {
           background-color: themed('colorBackground') !important;
           border-bottom: 1px solid themed('colorBackgroundBody');
       }
        width: 346px;
        height: 60px;
        display: flex;


        .case__status--critial{
            background:red;
        }
        .case__status{
            height: 100%;
            width: 12px;
        }
        .title-container{
            margin-left: -12px;
            justify-content: center;
           align-items: flex-start;
          @include themify($themes) {
            color: themed('colorText') !important;

          }
        }
        .case-investigation-header--title{
            max-width: 295px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            margin-bottom: 12px;
        }
        .case-investigation-header--details{
             div:first-child{
                        padding-left: 0;
                    }
                    div{
                        padding: 0 var(--element-padding-medium);
                        border-right: 1px solid var(--stroke-color);
                    }
        }

    }

    .investigation-list
    {
            height: 100%;
            @include themify($themes) {
                      background-color: themed('colorBackground') !important;

          }
            max-height: calc(100vh - 8em);
            overflow: auto;

    }
    .investigation-list__header{
        padding: 12px;
    }
    .investigation-list__list{
        list-style: none;
    }
    .investigation-list__alert{
       width: 100%;
       height: 78px;
       border: 1px solid transparent;
       color: red;
    }
    .investigation-list__event-count-container{
        width: 60px;
        min-width: 60px;
        position: relative;
        justify-content: center;
    }
    .investigation-list__event-count{
        user-select: none;
        color: red;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        border: 2px solid red;
        z-index: 1;
    }

    .investigation-list__main{
        grid-template-rows: repeat(2,1fr);
        grid-template-columns: repeat(2,1fr);
        width: 100%;
        padding: 3px 0;
    }

    .investigation-list__main--title{
        display: grid;
        color: red;
        grid-column: 1/3;
    }



   .investigation-list__event-count::after, .investigation-list__event-count::before{
        position: absolute;
        content: "";
        width: 2px;
        height: 35%;
        left: 29px;
        background: red;

    }


}