
.profile-view{
    .account-section-container{
        padding-top: 10px;
        padding-left: 20px;
        font-size: 12px;
    }
    .field-label{
        display: inline-block;
        vertical-align: middle;
        zoom: 1;
        width: 95px;
        font-weight: bolder;
        @include themify($themes) {
            color: themed('colorText');
        }
    }
    .field-value{
        font-family: RobotoMedium, "Arial Bold", sans-serif;
        display: inline-block;
        padding-left: 3%;
        @include themify($themes) {
            color: themed('colorText');
        }
        
    }
}

