.playbookContainer{

    .group{
        background: transparent;
        border: 0px;
    }
    a.bjs-powered-by{
        visibility: hidden;
    }
}
.highlight-overlay {
    background-color: green; 
    opacity: 0.4;
    pointer-events: none; /* no pointer events, allows clicking through onto the element */
  }

  .highlight:not(.djs-connection) .djs-visual > :nth-child(1) {
    fill: #34a951 !important;
  }
  .fail:not(.djs-connection) .djs-visual > :nth-child(1) {
    fill: #dd541e !important;
  }
  .playbookContainer{
      padding: 12px;
      margin-top: 12px;
  }