.react-fluid-table{
  @include themify($themes) {
    background: themed('colorBackground');
    border: 1px solid themed('colorBorder')
  }

}
.react-fluid-table-header{
  @include themify($themes) {
    background: themed('colorBackground');
    border: 1px solid themed('colorBorder')
  }

}
.react-fluid-table-row{
  @include themify($themes) {
    color: themed('colorText');

  }
}
.react-fluid-table-row {
  @include themify($themes) {
    border-bottom: 1px solid themed('colorBorder');

  }
}
.flexlayout__border_button--selected{
  @include themify($themes) {

    color:themed('colorText');

  }
}
.flexlayout__splitter{
  @include themify($themes) {
    background-color: themed('colorBorder');

  }
}
.flexlayout__splitter:hover{
  @include themify($themes) {
    background-color: themed('colorBorder');

  }
}



.globalFilterItem:not(.globalFilterItem-isDisabled){
  @include themify($themes) {
    background-color:  themed('colorBackground');
    color:themed('colorText');
    box-shadow: 0 1px 1px -1px rgba(152, 162, 179, 0.2), 0 3px 2px -2px themed('colorBorder'), inset 0 0 0 1px themed('colorBorder');

  }
}
.popover {
  @include themify($themes) {
    background-color:  themed('colorBackground') !important;

  }
}



.flexlayout__border_button:hover{
  @include themify($themes) {
    color:  themed('colorText');
    background:  themed('colorBorder');
  }
}

.add-widget-model{
  @include themify($themes) {
    color:  themed('colorText') !important;
    background-color:  themed('colorBackground') !important;
    display: flex;
    margin: 16px;
    width: calc(100% - 32px) !important;
    height: calc(100% - 32px) !important;
    min-width: 0;
    max-width: calc(100% - 32px) !important;
  }
}

.bottom-pane.horizontal.reflex-element{
  @include themify($themes) {
    
    background-color:  themed('colorBackground') !important;
  }
}
.MuiAppBar-colorDefault{
  @include themify($themes) {
    background-color:  themed('colorBackground') !important;
  }
}

.left-pane {
  @include themify($themes) {
    background-color:  themed('colorBackground') !important;
  }
  
}
.jstree-default .jstree-anchor{
  @include themify($themes) {
    color:  themed('colorText') !important;
  }
}
.reflex-container.vertical > .reflex-splitter{
  @include themify($themes) {
    border-right:  1px solid  themed('agGridHeader') !important;
    border-left:  1px solid  themed('agGridHeader') !important;
  }
}
.horizontal > .reflex-splitter{
  @include themify($themes) {
    border-bottom:  1px solid  themed('agGridHeader') !important;
    border-top:  1px solid  themed('agGridHeader') !important;
  }
}
.reflex-container.horizontal > .reflex-splitter:hover, .reflex-container.horizontal > .reflex-splitter.active{
  @include themify($themes) {
    border-right:  1px solid  themed('agGridHeader') !important;
    border-left:  1px solid  themed('agGridHeader') !important;
  }
}
.MuiTab-textColorPrimary.Mui-selected{
  color: #34a951  !important;
}
.MuiTabs-indicator{
  background-color: #34a951  !important;
}
.popover .popover-body{
  @include themify($themes) {
    background-color:  themed('colorBackground') !important;
  }
}
.threatIntelTd{
  color: #26a9aa  !important;
