.csstransforms aside {
    border-left: 24px solid transparent;
    
    @include themify($themes) {
        background: themed('colorFieldsBorder')  !important;
      }
    height: 100vh;
    div.sideContainer {
        height: 53px;
        @include themify($themes) {
            background: themed('colorHover')  !important;
          }
        & h3{
            
            position: absolute;
            font-size: 14px;
            transform-origin: 0 0;
            transform: rotate(
        90deg
        );
            @include themify($themes) {
                 background: themed('colorHover')  !important;
             } 
            font: bold 12px Sans-Serif;
            letter-spacing: 2px;
            text-transform: uppercase;
            color: bal;
            padding: 0px 10px;
            margin: 0 0 10px 0;
            line-height: 27px;
        }
    }
}
.sc-fzpans{
    width: 109%;

}
.logButton {
    @include themify($themes) {
        background: themed('colorHover')  !important;
        color: themed('colorText')  !important; 
    } 
    width: 4%;
    font-size: 13px;
    font-weight: bold;
    text-align: center;
    float: right;
    cursor: pointer;
}



 